<template>
    <div id="app">
        <div class="sign-bg">
			<div class="login-tabs">
				<div class="login-tabs-container">
					<template v-for="(tab, index) in tab_array">
						<div class="login-tab" v-bind:class="[index == tab_index ? 'login-tab-cur' : '']" v-on:click="tabToggle(index)">
							<span>{{ tab.name }}</span>
							<i class="icon-tri"></i>
						</div>
					</template>
				</div>
			</div>
		</div>
		<div class="login-form" v-bind:class="[tab_index == 1 ? 'hidden' : '']">
			<input type="" name="" class="input-sign" v-model="phone" placeholder="请输入手机号">
			<input type="password" name="" class="input-sign" v-model="password" placeholder="请输入密码">
			<button class="button-default button-login" v-on:click="signinHandler">登录</button>
			<a v-bind:href="signup_link" class="login-entry">或“注册”</a>
		</div>
		<div class="login-form" v-bind:class="[tab_index == 0 ? 'hidden' : '']">
			<input type="" name="" class="input-sign" v-model="phone" placeholder="请输入手机号">
			<div class="authcode-input">
				<input type="" name="" v-model="authcode" placeholder="请输入验证码" class="input-sign">
				<div class="authcode-trigger" v-if="code_sent">重新获取{{ timer }}</div>
				<div class="authcode-trigger" v-else v-on:click="sendcodeHandler">获取验证码</div>
			</div>
			<button class="button-default button-login" v-on:click="quickSigninHandler">登录</button>
			<a v-bind:href="signup_link" class="login-entry">或“注册”</a>
		</div>
		<transition name="fade">
			<Toast v-bind:msg="toast_text" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>

<script>
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toast_text: '',
	    	phone: '',
	    	authcode: '',
	    	timer: 60,
	    	code_sent: false,
	    	password: '',
    		tab_index: 0,
            signup_link: store.site_links.signup,
    		tab_array: [
    			{name: '账号密码登录'},
    			{name: '快捷登陆'}
    		]
    	}
    },
    methods:{
    	tabToggle: function(index){
    		this.tab_index = index;
    	},
    	signinHandler: function(){
    		var that = this;
    		var phone = utils.trim(that.phone);
    		var authcode = utils.trim(that.authcode);
    		var password = utils.trim(that.password);
    		var form_data = {};
    		if(phone == '' || phone == undefined){
    			that.toast_text = '手机号不能为空！';
    			that.showToast();
    			return;
    		}
    		if(!utils.checkphone(phone)){
    			that.toast_text = '请填写正确的手机号！';
    			that.showToast();
    			return;
    		}
    		if(password == '' || password == undefined){
    			that.toast_text = '密码不能为空！';
    			that.showToast();
    			return;
    		}
    		//form_data = that.getData();
    		store.login(phone, password).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    location.href = store.site_links.index;
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
    	},
    	quickSigninHandler: function(){
    		var that = this;
    		var phone = utils.trim(that.phone);
    		var authcode = utils.trim(that.authcode);
    		if(phone == '' || phone == undefined){
    			that.toast_text = '手机号不能为空！';
    			that.showToast();
    			return;
    		}
    		if(!utils.checkphone(phone)){
    			that.toast_text = '请填写正确的手机号！';
    			that.showToast();
    			return;
    		}
    		if(authcode == '' || authcode == undefined){
    			that.toast_text = '验证码不能为空！';
    			that.showToast();
    			return;
    		}
            store.quicklogin(phone, authcode).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    location.href = store.site_links.signinfo;
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
    	},
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 3000);
    	},
    	getData: function(){
    		var that = this;
    		var data = {};
    		if(that.tab_index == 0){
    			data.mobile = that.phone;
    			data.password = that.password;
    			return data;
    		}else if(that.tab_index == 1){

    		}
    	},
    	initTimer:function(){
		    var that = this;
		    that.timerInterval = setInterval(function(){
			    if(that.timer>0){
			        that.timer = that.timer-1;
			    }else{
			        that.stopTimer();
			    }
		    }, 1000);
		},
		stopTimer: function(){
			var that = this;
			clearInterval(that.timerInterval);
			that.timer = 60;
		},
    	sendcodeHandler: function(){
    		var that = this;
            var phone = utils.trim(that.phone);
            if(phone == '' || phone == undefined){
                that.toast_text = '手机号不能为空！';
                that.showToast();
                return;
            }
            if(!utils.checkphone(phone)){
                that.toast_text = '请填写正确的手机号！';
                that.showToast();
                return;
            }
            store.authcode('login', phone).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    that.code_sent = true;
                    that.$nextTick(function(){
                        that.initTimer();
                    });
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
    	}
    },
    components: {
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
@import '../../assets/css/sign.css';
</style>



